html,
body {
  height: 100%;
}

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

body {
  font-family: "IntroBook";
  font-size: 16px;
  color: #444b58;
  margin: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  display: block;
  width: 100%;
  height: 100%;
}

.btn-reset {
  border: none;
  cursor: pointer;
  background: transparent;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.disable-scroll {
  overflow: hidden;
  height: 100%;
  /*Для apple*/
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

._container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 15px;
}

.icon-menu {
  display: none;
}

.btn {
  padding: 22px 47px;
  border-radius: 4px;
  font-size: 16px;
  line-height: 16px;
  font-family: Intro;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: ease-in-out 0.3s;
}

._title {
  font-family: Intro;
  font-weight: 700;
  font-size: 30px;
}

.checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.checkbox:checked + span::after {
  opacity: 1;
}

.checkbox__text {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.checkbox__text:before {
  content: "";
  width: 24px;
  height: 24px;
  border: 1px solid #dbbba9;
  border-radius: 4px;
  display: inline-block;
  margin: 0 10px 0 0;
}

.checkbox__text:after {
  content: "";
  position: absolute;
  width: 15px;
  height: 12px;
  left: 5px;
  background: url("../img/checkbox.svg") center no-repeat;
  opacity: 0;
  transition: 0.3s;
}

.checkbox__text.error:before {
  outline: 1px solid red;
}

