.tooltip__icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 2px 4px 10px rgba(0, 13, 84, 0.2);
}

.tooltip__icon svg {
  width: 6px;
  height: 10px;
}

.tooltip__text {
  visibility: hidden;
  opacity: 0;
  z-index: 5;
  transform: translateY(-50%);
  position: absolute;
  left: -11px;
  bottom: calc(100% + 14px);
  padding: 10px 20px;
  width: 426px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 4px 8px 20px rgba(0, 13, 84, 0.2);
  font-size: 14px;
  line-height: 140%;
  will-change: transform;
  transition: 0.3s ease-out;
}

.tooltip__text::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 13px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 8px 0 8px;
  border-color: #fff transparent transparent transparent;
}

.tooltip._tooltip-open .tooltip__text {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

