.modal {
  --transition-time: 0.3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition-time);
  /* .modal__container */
  /* .modal__close */
  /* .modal__content */
}

.modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.modal__container {
  display: none;
  position: relative;
  width: 1140px;
  padding: 40px;
  text-align: left;
  vertical-align: middle;
  background-color: #fff;
  cursor: default;
  border-radius: 4px;
  margin: 50px auto;
}

.modal__close {
  font-size: 30px;
  padding: 10px;
  position: absolute;
  top: 5px;
  right: 5px;
  color: #444b58;
  cursor: pointer;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: var(--transition-time);
}

.fade {
  display: inline-block;
  opacity: 0;
  transition: var(--transition-time);
}

.fade.open-animate {
  opacity: 1;
  transition: var(--transition-time);
}

.fadeInUp {
  display: inline-block;
  transform: translateY(-150px);
  opacity: 0;
  transition: var(--transition-time);
}

.fadeInUp.open-animate {
  opacity: 1;
  transform: translateY(0);
  transition: var(--transition-time);
}

